@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.5.0/font/bootstrap-icons.css");


*{
    padding:0;
    margin:0;
    box-sizing: border-box;
}
body{
    background-color: #FAFAFA;
}
.container{
    max-width: 1000px;
}
.navbar{
    padding:0px;
}
.navbar .navbar-nav{
    margin-left: auto;
    display: flex;
    flex-direction: row;
}
.nav-item i{
    font-size: 1.5rem;
    color:black;
    padding:0px 5px;
}
.nav-item img{
    width:40px;
    height:40px;
}
.search{
    /* width:100%; */
    display: flex;
    justify-content: center;
}
.search input{
    width:200px;
    font-family: FontAwesome;
}
.story{
    display: block;
    margin-top: 20px;
    white-space: nowrap;
    overflow-x:auto ;
    
}
.story::-webkit-scrollbar{
    display: none;
}
.story .story-img{
    width:55px;
    height: 55px;
    border-radius: 50%;
    border:2px solid #C72D8E;
    margin:10px;
    display: inline-block;
}
.story .story-img img{
    border-radius: 50%;
    border:2px solid #fff;

    width:100%;
}
.post{
    margin-top: 30px;
}
.small-pic{
    width:40px;
    height:40px;
    border-radius: 50%;
    
}
h5{
    margin:0 10px;
    display: inline;
}
.head{
    background-color: #fff;
}
.post-img{
    width:100%;
    height:500px;
}

.icon-b{
   /* margin:20px; */
   
   margin-right:15px;
    font-size: 1.5rem;
    color:black;
    cursor: pointer;
}
.des{
    display: inline;
}
.single-post{
    margin-bottom: 20px;
}
.extra{
    float:right;
}
.red-color{
   color: #ED4956;
}
.right-side{
    margin-top:20px;
}
.temp{
    margin-top: 60px;
}
.check{
    border:none;
    background-color: #FAFAFA;
}
.side-bar-img{
    width:50px;
    height:50px;
    border-radius:50%;
}
p{
    margin:0;
}
.hindi{
    font-size: 0.8rem;
    color:grey;
}
.sub-h{
    padding:10px 10px;
    font-size: 1rem;
    font-weight:500;
}
.follow{
 text-decoration: none;
 padding:5px 20px;
 float:right;
 font-size: 0.7rem;
}
.side-username{
    font-size: 0.9rem;
    padding:5px 20px;
}
.sidebar-img{
    border-radius: 50%;
    margin:3px;
}


